// Modificar os métodos existentes para verificar o token e usar dados de exemplo como fallback

export const filaService = {
  async getClientesNaFila(): Promise<Cliente[]> {
    try {
      // Verificar se há token antes de fazer a requisição
      const token = localStorage.getItem("token")
      if (!token) {
        console.warn("Token não encontrado ao buscar clientes na fila, usando dados de exemplo")
        return clientesFilaExemplo
      }

      const response = await apiClient.get("/fila/clientes")
      return response.data
    } catch (error) {
      console.error("Erro ao buscar clientes na fila:", error)
      // Em caso de erro, retornar dados de exemplo
      return clientesFilaExemplo
    }
  },

  async getClientesEmAtendimento(): Promise<Atendimento[]> {
    try {
      // Verificar se há token antes de fazer a requisição
      const token = localStorage.getItem("token")
      if (!token) {
        console.warn("Token não encontrado ao buscar clientes em atendimento, usando dados de exemplo")
        return clientesAtendimentoExemplo
      }

      const response = await apiClient.get("/fila/atendimentos")
      return response.data
    } catch (error) {
      console.error("Erro ao buscar clientes em atendimento:", error)
      // Em caso de erro, retornar dados de exemplo
      return clientesAtendimentoExemplo
    }
  },

  async adicionarClienteNaFila(cliente: Omit<Cliente, "id" | "chegada" | "espera">): Promise<Cliente> {
    try {
      // Verificar se há token antes de fazer a requisição
      const token = localStorage.getItem("token")
      if (!token) {
        console.error("Token não encontrado ao adicionar cliente à fila")
        throw new Error("Você precisa estar autenticado para acessar este recurso")
      }

      const response = await apiClient.post("/fila/clientes", cliente)
      return response.data
    } catch (error) {
      console.error("Erro ao adicionar cliente na fila:", error)
      throw error
    }
  },

  async iniciarAtendimento(id: number, atendente = "Atendente"): Promise<Atendimento> {
    try {
      // Verificar se há token antes de fazer a requisição
      const token = localStorage.getItem("token")
      if (!token) {
        console.error("Token não encontrado ao iniciar atendimento")
        throw new Error("Você precisa estar autenticado para acessar este recurso")
      }

      const response = await apiClient.post(`/fila/atendimentos`, {
        clienteId: id,
        atendenteId: 1, // Assuming atendenteId is not relevant in the updated logic
        atendente: atendente,
      })
      return response.data
    } catch (error) {
      console.error("Erro ao iniciar atendimento:", error)
      throw error
    }
  },

  async finalizarAtendimento(id: number): Promise<void> {
    try {
      // Verificar se há token antes de fazer a requisição
      const token = localStorage.getItem("token")
      if (!token) {
        console.error("Token não encontrado ao finalizar atendimento")
        throw new Error("Você precisa estar autenticado para acessar este recurso")
      }

      await apiClient.put(`/fila/atendimentos/${id}/finalizar`)
    } catch (error) {
      console.error("Erro ao finalizar atendimento:", error)
      throw error
    }
  },

  async removerClienteDaFila(id: number): Promise<void> {
    try {
      // Verificar se há token antes de fazer a requisição
      const token = localStorage.getItem("token")
      if (!token) {
        console.error("Token não encontrado ao remover cliente da fila")
        throw new Error("Você precisa estar autenticado para acessar este recurso")
      }

      await apiClient.delete(`/fila/clientes/${id}`)
    } catch (error) {
      console.error("Erro ao remover cliente da fila:", error)
      throw error
    }
  },
}

export default filaService

export interface Cliente {
  id: number
  nome: string
  servico: string
  chegada: string
  espera: string
  prioridade: string
  telefone: string
  status: string
}

export interface Atendimento {
  id: number
  nome: string
  servico: string
  chegada: string
  inicio: string
  espera: string
  prioridade: string
  telefone: string
  status: string
  atendente: string
}
